body {

  .circle img,
  .comment-author .avatar{
      aspect-ratio: 1/1;

  }

  // jQuery Confirm
  .jconfirm{
    .jconfirm-scrollpane {
      width: 80%;
      margin-left: 10%;
    }
  }
  // Modal
  .modal {
    position: fixed;
    top: 0px;
    left: 0px;
    height: 100%;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 9999;
    padding-left: 2rem;
    padding-right: 2rem;
    display: none;
    .container {
      height: 80vh;
      margin-top: 10vh;
      overflow-y: auto;
    }
    .dashicons {
      color: #fff;
    }
  }

  .ratio-3x4 {
    width: 100%;
    aspect-ratio: 3/4;
  }

  .bg-image-cover {
    background-size: cover !important;
    background-position: center !important;
  }

  //date picker
  .ui-datepicker{
    .ui-datepicker-title {
      display: flex;
      justify-content: space-between;
      select {
        padding: 0rem 1rem;
      }
    }
    .ui-datepicker-buttonpane{
      button {
        font-size: 0.75rem;
        padding: 0rem 1rem;
      }
    }

  }
  // Meta Box
  .rwmb-input{
    .rwmb-button {
      background-color: $primary;
      color: $white;
      &:hover {
        background-color: shade-color($primary, 20%);
      }
    }
  }

  // Table
  table{

    td,th{
      vertical-align: middle;
      text-align: center;
    }
  }
}

